import { ElmArch } from "./ElmRx";
/**
 * For Angular application, use this service to drive your components.
 *
 * @export
 * @abstract
 * @class ElmArchService
 * @template TModel
 * @template TMsgType
 */
export class ElmArchService {
    constructor(debug = false) {
        this.arch = new ElmArch();
        this.model$ = this.arch.begin(this.initModel(), this.update(), debug);
    }
    get model() {
        return this.model$.value;
    }
    send(msg) {
        this.arch.send(msg);
    }
    destroy() {
        this.arch.stop();
    }
}
